vbar-default .navbar-nav>li.open>a:hover, header .navbar-default .navbar-nav>li.open>a:focus {\r\n  background: #fff;\r\n  border: 1px solid #fb6c6c;\r\n}\r\n\r\nheader .navbar-nav .dropdown-menu {\r\n  top: 57px;\r\n  border-radius: 3px;\r\n  border-color: #fb6c6c;\r\n}\r\n\r\nheader .navbar-nav .dropdown-menu:after, header .navbar-nav .dropdown-menu:before {\r\n  bottom: 100%;\r\n  right: 30px;\r\n  border: solid transparent;\r\n  content: \" \";\r\n  height: 0;\r\n  width: 0;\r\n  position: absolute;\r\n  pointer-events: none;\r\n}\r\n\r\nheader .navbar-nav .dropdown-menu:after {\r\n  border-color: rgba(240, 253, 255, 0);\r\n  border-bottom-color: #fff;\r\n  border-width: 7px;\r\n}\r\n\r\nheader .navbar-nav .dropdown-menu:before {\r\n  border-color: rgba(194, 225, 245, 0);\r\n  border-bottom-color: #fb6c6c;\r\n  border-width: 8px;\r\n  margin-right: -1px;\r\n}\r\n\r\nheader .navbar-brand {\r\n  padding: 0;\r\n  height: auto;\r\n}\r\n\r\nheader .navbar-brand img {\r\n  top: 7px;\r\n  max-width: 60px;\r\n}\r\n\r\n.title-header {\r\n  color: #fb6c6c !important;\r\n}\r\n\r\n.imgHeader {\r\n  position: relative;\r\n  animation: mymove 2s infinite;\r\n}\r\n\r\n@keyframes mymove {\r\n  from {\r\n    top: 0px;\r\n  }\r\n  to {\r\n    top: 10px;\r\n  }\r\n}\r\n\r\n/*\r\n  * Banner \r\n  */\r\n\r\n.banner {\r\n  padding-top: 150px;\r\n  padding-bottom: 150px;\r\n  text-align: center;\r\n}\r\n\r\n.banner h2 {\r\n  margin-top: 0;\r\n  margin-bottom: 0;\r\n  font-size: 28px;\r\n  line-height: 35px;\r\n  color: #eee;\r\n  text-transform: capitalize;\r\n}\r\n\r\n.banner h3 {\r\n  margin-top: 20px;\r\n  margin-bottom: 25px;\r\n  font-size: 20px;\r\n  line-height: 25px;\r\n  color: #ddd;\r\n  text-transform: uppercase;\r\n  letter-spacing: 0.055em;\r\n}\r\n\r\n.banner .btn {\r\n  padding: 7px 25px;\r\n  font-size: 14px;\r\n  border-radius: 30px;\r\n}\r\n\r\n.banner {\r\n  background-repeat: no-repeat;\r\n  background-size: 100%;\r\n}\r\n\r\n/*\r\n  * default heading\r\n  */\r\n\r\n.default-heading {\r\n  margin-bottom: 30px;\r\n  text-align: center;\r\n}\r\n\r\n.default-heading h2 {\r\n  margin-top: 0;\r\n  margin-bottom: 20px;\r\n  font-size: 32px;\r\n}\r\n\r\n.default-heading p {\r\n  font-size: 14px;\r\n  line-height: 26px;\r\n  text-transform: capitalize;\r\n  letter-spacing: 0.055em;\r\n}\r\n\r\n/*\r\n  * works\r\n  */\r\n\r\n.works {\r\n  padding-top: 120px;\r\n  padding-bottom: 120px;\r\n}\r\n\r\n.works .work-item {\r\n  max-width: 360px;\r\n  margin: 0 auto;\r\n  margin-top: 30px;\r\n  text-align: center;\r\n}\r\n\r\n.works .work-item h3 {\r\n  margin-top: 20px;\r\n  font-size: 22px;\r\n}\r\n\r\n.works .work-item .org {\r\n  display: block;\r\n  font-size: 13px;\r\n  font-style: italic;\r\n  text-transform: uppercase;\r\n}\r\n\r\n/*\r\n  * game description\r\n  */\r\n\r\n.imgDescription {\r\n  width: 100%;\r\n  background-repeat: no-repeat;\r\n}\r\n\r\n.imgGame {\r\n  width: 49%;\r\n  background-repeat: no-repeat;\r\n}\r\n\r\n.boxDescription {\r\n  padding: 0;\r\n}\r\n\r\n.specs {\r\n  text-align: left;\r\n}\r\n\r\n/*\r\n  * footer\r\n  */\r\n\r\nfooter {\r\n  padding: 50px 0;\r\n  background: #e32b2b;\r\n  text-align: center;\r\n}\r\n\r\nfooter p {\r\n  font-size: 13px;\r\n  line-height: 25px;\r\n  color: #fff;\r\n}\r\n\r\nfooter p a {\r\n  font-size: 14px;\r\n  line-height: 26px;\r\n  text-transform: uppercase;\r\n  letter-spacing: 0.055em;\r\n  color: #fff;\r\n}\r\n\r\nfooter p a:hover {\r\n  color: #fb6c6c;\r\n}\r\n\r\nfooter img {\r\n  max-width: 150px !important;\r\n  margin: 15px auto;\r\n  border-radius: 4px;\r\n}\r\n\r\nfooter .social a {\r\n  display: inline-block;\r\n  margin: 0 2px;\r\n  margin-top: 5px;\r\n  padding: 2px;\r\n  width: 35px;\r\n  height: 35px;\r\n  line-height: 35px;\r\n  background: #fff;\r\n  font-size: 14px;\r\n  border-radius: 4px;\r\n}\r\n\r\nfooter .social a i {\r\n  display: block;\r\n  width: 100%;\r\n  height: 100%;\r\n  line-height: 30px;\r\n  border-radius: 3px;\r\n  border: 1px dashed #e32b2b;\r\n}\r\n\r\nfooter .copy-right {\r\n  margin-top: 10px;\r\n}\r\n\r\n.volver {\r\n  font-size: 20px;\r\n  margin-top: 15%;\r\n}\r\n\r\n.custom-select {\r\n  display: flex;\r\n  justify-content: end;\r\n}\r\n\r\n.favorite {\r\n  color: red;\r\n}\r\n\r\n.no-favorite {\r\n  color: #fb6c6c;\r\n}\r\n\r\n.ir-favorito {\r\n  float: right;\r\n}\r\n\r\n.imgNotFound {\r\n  width: 30%;\r\n}\r\n\r\n.parrafo-not-found {\r\n  font-size: 20px;\r\n}\r\n\r\n.boton {\r\n  width: 80px;\r\n  background-color: #fb6c6c;\r\n  margin: 5px;\r\n  padding: 10px;\r\n  -webkit-border-radius: 50px;\r\n  -moz-border-radius: 50px;\r\n  border-radius: 50px;\r\n  font-size: 11px;\r\n  line-height: 32px;\r\n  text-transform: uppercase;\r\n  float: left;\r\n  color: white;\r\n}\r\n\r\n.boton:hover {\r\n  opacity: 0.50;\r\n  -moz-opacity: .50;\r\n  filter: alpha (opacity=50);\r\n}\r\n\r\n.box-category {\r\n  display: flex;\r\n  justify-content: center;\r\n  flex-wrap: wrap;\r\n}\r\n\r\n.btnTop {\r\n  position: fixed;\r\n  bottom: 4%;\r\n  right: 4%;\r\n}\r\n\r\n/*Flecha para hacer la pagina hacia arriba*/\r\n\r\n.ir-arriba {\r\n  background-repeat: no-repeat;\r\n  font-size: 20px;\r\n  color: black;\r\n  cursor: pointer;\r\n  position: fixed;\r\n  bottom: 10px;\r\n  right: 10px;\r\n  z-index: 2;\r\n}\r\n\r\n\r\n/* LOADER */\r\n\r\n.loader {\r\n  position: relative;\r\n  width: 60px;\r\n  height: 60px;\r\n  border-radius: 50%;\r\n  margin: 75px;\r\n  display: inline-block;\r\n  vertical-align: middle;\r\n}\r\n\r\n.loader-star {\r\n  position: absolute;\r\n  top: calc(50% - 12px);\r\n}\r\n\r\n\r\n\r\n.loader-5 {\r\n  border: 8px dotted #E06E49;\r\n  -webkit-transition: all 1s ease;\r\n  -o-transition: all 1s ease;\r\n  transition: all 1s ease;\r\n  -webkit-animation: dotted-spin 1.2s linear infinite;\r\n  animation: dotted-spin 1.2s linear infinite;\r\n  border-bottom-width: 1px;\r\n  border-bottom-color: #E06E49;\r\n  border-left-width: 2px;\r\n  border-left-color: #E06E49;\r\n  border-top-width: 3px;\r\n  border-right-width: 4px;\r\n  border-top-color: #E06E49;\r\n}\r\n\r\n.loader-5 .loader-pacman, .loader-pacman {\r\n  position: absolute;\r\n  top: 40px;\r\n  left: 25px;\r\n  width: 0px;\r\n  height: 0px;\r\n  border-right: 12px solid transparent;\r\n  border-top: 12px solid #ECA40D;\r\n  border-left: 12px solid #ECA40D;\r\n  border-bottom: 12px solid #ECA40D;\r\n  border-top-left-radius: 12px;\r\n  border-top-right-radius: 12px;\r\n  border-bottom-left-radius: 12px;\r\n  border-bottom-right-radius: 12px;\r\n}\r\n\r\n@-webkit-keyframes dotted-spin {\r\n  0% {\r\n      -webkit-transform: rotate(0deg);\r\n      transform: rotate(0deg);\r\n  }\r\n  100% {\r\n      -webkit-transform: rotate(-360deg);\r\n      transform: rotate(-360deg);\r\n  }\r\n}\r\n\r\n@keyframes dotted-spin {\r\n  0% {\r\n      -webkit-transform: rotate(0deg);\r\n      transform: rotate(0deg);\r\n  }\r\n  100% {\r\n      -webkit-transform: rotate(-360deg);\r\n      transform: rotate(-360deg);\r\n  }\r\n}\r\n\r\n/*\r\n  * Tablets \r\n  */\r\n\r\n@media (max-width: 767px) {\r\n  header .navbar-nav .dropdown-menu:after, header .navbar-nav .dropdown-menu:before {\r\n    display: none;\r\n  }\r\n  .contact .contact-container {\r\n    max-width: 360px;\r\n    margin: 0 auto;\r\n  }\r\n  .contact .contact-container form {\r\n    margin-bottom: 30px;\r\n  }\r\n  .contact .contact-container .map {\r\n    margin-top: 30px;\r\n  }\r\n}\r\n\r\n@media (max-width: 425px) {\r\n  .banner {\r\n    background-size: auto;\r\n  }\r\n  .ir-favorito {\r\n      float: none;\r\n      margin-bottom: 30px;\r\n  }\r\n  .container{\r\n    text-align: center;\r\n  }\r\n}\r\n\r\n\r\n"],"sourceRoot":""}]);
